import { useEffect, useState, useCallback } from 'react'

export const useDarkMode = () => {
  const [theme, setTheme] = useState<'light' | 'dark'>('light')
  const [mounted, setMounted] = useState(false)

  //    
  const handleSystemThemeChange = useCallback((e: MediaQueryListEvent) => {
    if (!localStorage.getItem('theme')) {
      setTheme(e.matches ? 'dark' : 'light')
    }
  }, [])

  //   
  useEffect(() => {
    try {
      const savedTheme = localStorage.getItem('theme') as
        | 'light'
        | 'dark'
        | null
      const prefersDark = window.matchMedia(
        '(prefers-color-scheme: dark)',
      ).matches

      if (savedTheme && (savedTheme === 'light' || savedTheme === 'dark')) {
        setTheme(savedTheme)
      } else if (prefersDark) {
        setTheme('dark')
      }

      //     
      const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)')
      if (mediaQuery.addEventListener) {
        mediaQuery.addEventListener('change', handleSystemThemeChange)
      } else {
        //   
        mediaQuery.addListener(handleSystemThemeChange)
      }

      setMounted(true)

      // 
      return () => {
        if (mediaQuery.removeEventListener) {
          mediaQuery.removeEventListener('change', handleSystemThemeChange)
        } else {
          mediaQuery.removeListener(handleSystemThemeChange)
        }
      }
    } catch (error) {
      console.warn('Dark mode initialization failed:', error)
      setTheme('light') //  fallback
      setMounted(true)
    }
  }, [handleSystemThemeChange])

  //  
  useEffect(() => {
    if (mounted) {
      try {
        localStorage.setItem('theme', theme)
        document.documentElement.setAttribute('data-theme', theme)

        //     (  UI)
        const themeColorMeta = document.querySelector(
          'meta[name="theme-color"]',
        )
        if (themeColorMeta) {
          themeColorMeta.setAttribute(
            'content',
            theme === 'dark' ? '#1a1a1a' : '#ffffff',
          )
        }
      } catch (error) {
        console.warn('Theme application failed:', error)
      }
    }
  }, [theme, mounted])

  //  
  const toggleTheme = useCallback(() => {
    setTheme(prevTheme => (prevTheme === 'light' ? 'dark' : 'light'))
  }, [])

  //   
  const setSpecificTheme = useCallback((newTheme: 'light' | 'dark') => {
    setTheme(newTheme)
  }, [])

  //   
  const resetToSystemTheme = useCallback(() => {
    try {
      localStorage.removeItem('theme')
      const prefersDark = window.matchMedia(
        '(prefers-color-scheme: dark)',
      ).matches
      setTheme(prefersDark ? 'dark' : 'light')
    } catch (error) {
      console.warn('Reset to system theme failed:', error)
    }
  }, [])

  return {
    theme,
    isDarkMode: theme === 'dark',
    toggleTheme,
    setSpecificTheme,
    resetToSystemTheme,
    mounted,
  }
}
